import {
  createBrowserRouter,
  RouterProvider
} from "react-router-dom";
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Index from './Admin/Index';
import AdminLogin from './Admin/screens/AdminLogin';
import AdminRoot from './Admin/screens/AdminRoot';
import Category from './Admin/screens/Category/Category';
import Country from './Admin/screens/Country/Country';
import DashBoard from './Admin/screens/DashBoard/DashBoard';
import SubCategory from './Admin/screens/SubCategory/SubCategory';
import User from './Admin/screens/User/User';
import './App.css';
import { AppContextProvider } from './context/AppContext';
import Root from './Root';
import HomeScreen from './Screens/Home/HomeScreen';
import Posts from "./Screens/Posts/Posts";

const router = createBrowserRouter(
  [
    {
      path: '/',
      element: <Root />,
      children: [
        {
          path: '/',
          element: <HomeScreen />,
          children: [
            {
              path: "/",
              Component: () => <Posts />
            },
            {
              path: "/:id",
              Component: () => <Posts />
            },
            {
              path: '/country/:id/:stateId',
              Component: () => <Posts />
            },
            {
              path: '/country/:id/state/:stateId/city/:cityId',
              Component: () => <Posts />
            },
            {
              path: '/country/:id/state/:stateId/city/:cityId/area/:areaId',
              Component: () => <Posts />
            },
            {
              path: '/category/:categoryId',
              Component: () => <Posts />
            },
            {
              path: '/category/:categoryId/sub-category/:subCategoryId',
              Component: () => <Posts />
            },
            {
              path: '/post/country/:id/category/:categoryId',
              Component: () => <Posts />
            },
            {
              path: '/post/country/:id/state/:stateId/category/:categoryId',
              Component: () => <Posts />
            },
            {
              path: '/post/country/:id/state/:stateId/city/:cityId/category/:categoryId',
              Component: () => <Posts />
            },
            {
              path: '/post/country/:id/state/:stateId/city/:cityId/area/:areId/category/:categoryId',
              Component: () => <Posts />
            },
          ]
        }
      ]
    },
    {
      path: "/admin",
      element: <Index />,
    },
    {
      path: "/admin/login",
      element: <AdminLogin />
    },
    {
      path: "/admin/dashboard",
      element: <AdminRoot />,
      children: [
        {
          path: "/admin/dashboard",
          element: <DashBoard />
        },
        {
          path: "/admin/dashboard/users",
          element: <User />
        },
        {
          path: "/admin/dashboard/country",
          element: <Country />
        },
        {
          path: "/admin/dashboard/category",
          element: <Category />
        },
        {
          path: "/admin/dashboard/sub-category",
          element: <SubCategory />
        }
      ]
    }
  ]
);




function App() {

  return (
    <AppContextProvider>
      <RouterProvider router={router} />
      <ToastContainer />
    </AppContextProvider>
  );
}

export default App;



