import { ArrowDownward, ArrowUpward, Message, MoreVert, PlayCircle, Share } from '@mui/icons-material';
import { useContext, useEffect } from 'react';
import { useLocation, useParams } from 'react-router-dom';
import UserProfile from '../../Assets/Images/userProfile.png';
import { useLiveStream } from '../../LiveSdk/Context/LiveStreamContext';
import { AppContext } from '../../context/AppContext';
import './style.css'
function Posts() {
  const appContext = useContext(AppContext)
  const local = useLocation();
  const liveStream = useLiveStream();

  const { id, stateId, cityId, areaId, categoryId, subCategoryId } = useParams();
  useEffect(() => {
    console.log(local.pathname)
    if (local.pathname.startsWith("/category/")) {
      if (categoryId !== undefined) {
        appContext?.onFetchPostsByCategory!(categoryId);
      } else if (categoryId !== undefined && subCategoryId !== undefined) {
        appContext?.onFetchPostsBySubCategory!(categoryId, subCategoryId);
      }
    } else if (local.pathname.startsWith("/post/")) {
      if (id != undefined && categoryId != undefined && stateId == undefined) {
        appContext?.onFetchPostsByCountryAndCategory!(id, categoryId);
      } else if (id != undefined && stateId != undefined && categoryId !== undefined) {
        appContext?.onFetchPostsByCountryStateAndCategory!(id, stateId!, categoryId);
      } else if (id != undefined && stateId != undefined && categoryId !== undefined && cityId !== undefined) {
        appContext?.onFetchPostsByCountryStateCityAndCategory!(id, stateId!, cityId, categoryId);
      } else if (id != undefined && stateId != undefined && categoryId !== undefined && cityId !== undefined && areaId !== undefined) {
        appContext?.onFetchPostsByCountrySateSityAreaAndCategory!(id, stateId!, cityId, areaId, categoryId);
      }
    } else {
      if (id == undefined) {
        appContext?.onFetchPosts!("all")
      } else if (id !== undefined && stateId == undefined) {
        appContext?.onFetchPosts!(id);
      } else if (id !== undefined && stateId !== undefined) {
        appContext?.onFetchPostsByState!(id, stateId)
      } else if (id !== undefined && stateId !== undefined && cityId !== undefined) {
        appContext?.onFetchPostsByCity!(id, stateId, cityId)
      } else if (id !== undefined && stateId !== undefined && cityId !== undefined && areaId !== undefined) {
        appContext?.onFetchPostsByArea!(id, stateId, cityId, areaId)
      }
    }
  }, [id, stateId, cityId, areaId, categoryId, subCategoryId,local.pathname]);
  const onPlayStream = async (id: string) => {
    if (appContext?.localUser == null) {
      appContext?.customToast!("Please Login To Go Live");
    } else {
      const data = {
        name: appContext.localUser.username,
        id: appContext.localUser._id,
      }
      await liveStream?.onLogin!(data);
      appContext?.setIsGoLive!(true);
      await liveStream?.onJoin!(id, { isAdmin: false });
    }
  }

  // console.log(appContext?.localUser)

  return (


    <div style={{ marginTop: 20 }}>
      {
        appContext?.filtterPost == null ? <p style={{ textAlign: 'center', color: "#fff", paddingBlock: 20 }}>Please Wait...</p> :
          appContext?.filtterPost.length > 0 ?
            appContext?.filtterPost?.map((v, index) => {
              return <div key={index} className='postCard'>
                <div className='postHeader' style={{ display: 'flex' }}>
                  <div className='author' >
                    <img src={UserProfile} />
                    <p>{v.user?.username}</p>
                  </div>
                  <div style={{
                    display: 'flex',
                    // width: "7rem",
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    position: 'relative'
                  }}>{
                      v.isLive && <p style={{
                        border: '1px solid red',
                        paddingInline: "14px",
                        color: '#fff',
                        fontWeight: 600,
                        borderRadius: "4px",
                      }}>Live</p>

                    }
                    <MoreVert onClick={() => {
                      if (document.getElementById('postOption' + index)?.style.getPropertyValue("display") === "block") {
                        document.getElementById('postOption' + index)!.style.display = "none";
                      } else {
                        document.getElementById('postOption' + index)!.style.display = "block";
                      }
                    }} sx={{ color: "white" }} >
                    </MoreVert>
                    <ul className='postOption' id={'postOption' + index}>
                      {appContext?.localUser!._id == v.user._id && <li><span onClick={() => appContext.onDeletePost!(v._id)}>Delete</span></li>}
                      <li><span>Report</span></li>
                    </ul>
                  </div>
                </div>
                <div className='postBody'>
                  <p>{v.postTitle}</p>
                  {v.recordedUrl !== "" ?
                    <video poster={v.postThumbnail} controls>
                      <source src={v.recordedUrl} type='video/webm'></source>
                    </video>
                    :
                    <div>

                      {v.postThumbnail && <div style={{ position: "relative" }}>
                        <img src={v.postThumbnail} />
                        <div className='playBtnDiv'>
                          <PlayCircle
                            onClick={() => { v.isLive ? onPlayStream(v._id) : appContext?.customToast!("You can not play the stream") }}
                            style={{ fontSize: 50, color: "#fff", cursor: "pointer" }} />
                        </div>
                      </div>}
                    </div>
                  }
                </div>
                <div className="postFooter" style={{ marginTop: 10 }}>
                  <button>
                    <ArrowUpward />
                    <span>48k</span>
                    <ArrowDownward />
                  </button>
                  <button>
                    <Message />
                    <span>717</span>
                  </button>
                  <button>
                    <Share />
                    <span>Share</span>
                  </button>
                </div>
                <hr style={{ background: "#777" }} />
              </div>
            })
            : <p style={{ textAlign: 'center', color: "#fff", paddingBlock: 20 }}>No Post Live Or Recorded</p>
      }
    </div>
    // </div>
  )
}

export default Posts