const SideBar = () => {
  const appContext = useContext(AppContext);
  const n = useNavigate();
  const [countryId, setCountryId] = useState("");
  const [stateId, setStateId] = useState("");
  const [cityId, setCityId] = useState("");
  const [areaId, setaAeaId] = useState("");
  const [categoryId, setCategoryId] = useState("");


  const onMenuClick = (menuType: "menu" | "dropdown", id: string) => {
    if (menuType === "dropdown") {
      if (document.getElementById(id)?.style.getPropertyValue("display") === "block") {
        document.getElementById(id)!.style.display = "none";
      } else {
        document.getElementById(id)!.style.display = "block";
      }
    }
  }

  const onClickCategory = (id: string) => {
    if (countryId != '' && stateId == '') {
      n("/post/country/" + countryId + "/category/" + id);
    } else if (countryId != '' && stateId != '' && cityId == '') {
      n("/post/country/" + countryId + "/state/" + stateId + "/category/" + id);
    } else if (countryId != '' && stateId != '' && cityId != '' && areaId == '') {
      n("/post/country/" + countryId + "/state/" + stateId + "/city/" + cityId + "/category/" + id);
    } else if (countryId != '' && stateId != '' && cityId != '' && areaId != '') {
      n("/post/country/" + countryId + "/state/" + stateId + "/city/" + cityId + "/area/" + areaId + "/category/" + id);
    } else {
      n("/category/" + id);
    }
  }



  return (
    <div className="sideBar" id='sideBar'>
      <div className='menu'>
        {
          menus.map((v, i) => {
            return <div key={i}>
              <img src={v.icon} alt='icon' />
              <Link style={{ color: "#ccc" }} to={"/"}>{v.title}</Link>
            </div>
          })
        }
      </div>
      <div className='divider'></div>
      <div className='headingBox'>
        <p>PLACSES</p>
      </div>
      <div className='dropdownMenu menu'>
        {
          appContext?.allFetchCountries?.map((v, i) => {
            const state = v.state;
            return <div className='menuBox' key={i}>
              <div className='menuItem'>
                <Flag sx={{ mx: 1 }} />
                <Link
                  onClick={() => setCountryId(v.country)}
                  style={{ color: "#ccc" }} to={"/" + v.country} >{v.country}</Link>
                {state?.length! > 0 ? <ArrowForwardIos onClick={() => { state?.length! > 0 ? onMenuClick("dropdown", `menuOpen${i}`) : console.log("Not") }} className='arrowIcon' /> : null}
              </div>
              <div className='dropDownDiv' style={{ width: "100%", marginLeft: 0 }} id={`menuOpen${i}`}>
                {
                  state?.map((menu: StateType, index: number) => {
                    const cites = menu.city;
                    return <div className='dropdownMenu menu' style={{ width: '100%' }} key={index}>
                      <div className='menuItem'>
                        <Map sx={{ mx: 1 }} />
                        <Link
                          onClick={() => { setCountryId(v.country); setStateId(menu.state) }}
                          style={{ color: "#ccc" }} to={"/country/" + v.country + "/" + menu.state}>{menu.state}</Link>
                        {cites?.length! > 0 ? <ArrowForwardIos onClick={() => { cites?.length! > 0 ? onMenuClick("dropdown", `menuOpen${menu._id}`) : console.log("Not") }} className='arrowIcon' /> : null}
                      </div>
                      <div className='dropDownDiv' id={`menuOpen${menu._id}`}>
                        {
                          cites?.map((city: CityType, ci) => {
                            const areas = city.area;
                            return <div className='dropdownMenu menu' style={{ width: '100%', paddingLeft: 0, paddingRight: 0, }} key={city._id}>
                              <div
                                style={{ width: '100%', paddingLeft: 0, paddingRight: 0 }}
                                className='menuItem'>
                                <LocationCity sx={{ mx: 1 }} />
                                <Link
                                  onClick={() => { setCountryId(v.country); setStateId(menu.state); setCityId(city.city) }}
                                  style={{ color: "#ccc" }} to={"/country/" + v.country + "/state/" + menu.state + "/city/" + city.city}>{city.city}</Link>
                                {areas?.length! > 0 ? <ArrowForwardIos onClick={() => { areas?.length! > 0 ? onMenuClick("dropdown", `menuOpen${city._id}`) : console.log("Not") }} className='arrowIcon' /> : null}
                              </div>
                              <div className='dropDownDiv' id={`menuOpen${city._id}`}>
                                {
                                  areas?.map((area, ai) => {
                                    return <Link
                                      onClick={() => { setCountryId(v.country); setStateId(menu.state); setCityId(city.city); setaAeaId(area.area) }}
                                      style={{ color: "#ccc" }} to={"/country/" + v.country + "/state/" + menu.state + "/city/" + city.city + "/area/" + area.area} key={area._id}>{area.area}</Link>
                                  })
                                }
                              </div>
                            </div>
                          })
                        }
                      </div>

                    </div>
                  })
                }
              </div>
            </div>
          })
        }
      </div>
      <div className='divider'></div>
      <div className='headingBox'>
        <p>Topics</p>
      </div>
      {appContext?.menuTopics == null ? <p></p> : <div className='dropdownMenu menu'>
        {
          appContext?.menuTopics.map((v: any, k) => {
            return <div className='menuBox' key={k}>
              <div className='menuItem'>
                <ContentCopy sx={{ fontSize: 20, marginInline: 1 }} />
                {/* to={"/category/" + v.title} */}
                <span  onClick={() => onClickCategory(v.title)} style={{ color: "#ccc" }} >{v.title}</span>
                {v.subcategory.length > 0 ? <ArrowForwardIos onClick={() => { v.subcategory?.length > 0 ? onMenuClick("dropdown", `menuOpen${v._id}`) : console.log("dd") }} className='arrowIcon' /> : null}
              </div>
              {
                v.subcategory?.length > 0 ?

                  
                    v.subcategory?.map((menu: any, index: number) => {
                      return <div className='dropDownDiv' id={`menuOpen${v._id}`}> <Link style={{ color: "#ccc" }} to={"/category/" + v.title + "/sub-category/" + menu.title} key={index}>{menu.title}</Link>
                      </div>

                    })
                  
                  : null
              }

            </div>
          })
        }
      </div>}
    </div>
  )
}


export default SideBar;